<template>
  <div class="md-layout text-center">
    <div
      class="md-layout-item md-size-33 md-medium-size-50 md-small-size-70 md-xsmall-size-100"
    >
      <login-card header-color="danger">
        <h4 slot="title" class="title">Log in</h4>
        <md-button
          slot="buttons"
          href="#facebook"
          class="md-just-icon md-simple md-white"
        >
          <i class="fab fa-facebook-square"></i>
        </md-button>
        <md-button
          slot="buttons"
          href="#twitter"
          class="md-just-icon md-simple md-white"
        >
          <i class="fab fa-twitter"></i>
        </md-button>
        <md-button
          slot="buttons"
          href="#google"
          class="md-just-icon md-simple md-white"
        >
          <i class="fab fa-google-plus-g"></i>
        </md-button>
        <p slot="description" class="description">O acceder con credenciales</p>
        <md-field class="md-form-group" slot="inputs">
          <md-icon>face</md-icon>
          <label>Usuario</label>
          <md-input v-model="usuario"></md-input>
        </md-field>
        <md-field class="md-form-group" slot="inputs">
          <md-icon>lock_outline</md-icon>
          <label>Contraseña...</label>
          <md-input type="password" v-model="pass"></md-input>
        </md-field>
        <span v-if="error" slot="validaciones" class="validaciones">{{error}}</span>
        <md-button @click="acceder" slot="footer" class="md-simple md-danger md-lg">
          Acceder
        </md-button>
      </login-card>
    </div>
  </div>
</template>
<script>
import { LoginCard } from "@/components";
import { mapMutations,mapState } from 'vuex'
import {urlBase} from "../../urlBase";
export default {
  components: {
    LoginCard
  },
  data() {
    return {
      usuario: null,
      pass: null,
      url:urlBase,
      error:''
    };
  },
  created(){
    if(this.token!=''){
      this.$router.push('/social')
    }
  },
  methods:{
    async acceder(){
      const { data } = await axios.post(`${this.url}/login`,{usuario:this.usuario,pass:this.pass});
      if(data){
        this.setDataLogin({usuario:data.usuario.usuario,nombre:data.usuario.nombre,foto:data.usuario.foto,token:data.token})
        this.$router.push('/social')
      }
      else{
        this.error = "Datos incorrectos, intente de nuevo"
        this.logout()
      }
    },...mapMutations(['setDataLogin','logout'])
  },
  computed:{
    ...mapState(['token'])
  }
};
</script>

<style lang="scss">
  .md-card-header-danger{
    // background: #e21321 !important;
  }
  .validaciones{
    color: red;
  }
</style>
